* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background: #FFF;
  font-family: 'Montserrat', serif;
  font-size: 18px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  color: #000;
  position: relative; }

ul {
  list-style: none; }

a,
a:hover {
  text-decoration: none; }

::selection {
  text-shadow: none;
  background: #fed136; }

input,
button,
textarea,
select {
  outline: none !important; }

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px; }

.wrapper {
  overflow: hidden; }

section {
  padding: 110px 0;
  min-height: 100vh; }
  @media (max-width: 767.98px) {
    section {
      padding: 50px 0; } }

.title {
  font-size: 25px;
  letter-spacing: 3px;
  text-transform: uppercase;
  line-height: 32px;
  font-weight: 700;
  color: #2b2b2b;
  text-align: center;
  position: relative;
  font-family: 'Fira Code', monospace; }
  .title span {
    position: absolute;
    top: 45px;
    left: 50%;
    transform: translateX(-50%);
    width: 45px;
    height: 5px;
    border-radius: 2.5px;
    background: #ee4d45; }
  .title:after {
    content: "";
    display: block;
    width: 170px;
    height: 2px;
    background: #2b2b2b;
    margin: 15px auto; }

.subtitle {
  font-size: 18px;
  font-style: italic;
  font-weight: 300;
  line-height: 25px;
  text-align: center;
  margin-bottom: 40px; }

.typed-text {
  font-weight: 600;
  color: #dd7732; }

.cursor.typing {
  animation: none; }

.cursor {
  display: inline-block;
  width: 3px;
  background-color: #ccc;
  margin-left: 0.1rem;
  animation: blink 1s infinite; }

@keyframes blink {
  0% {
    background-color: #ccc; }
  49% {
    background-color: #ccc; }
  50% {
    background-color: transparent; }
  99% {
    background-color: transparent; }
  100% {
    background-color: #ccc; } }

.fixed-top {
  position: fixed !important;
  top: 0 !important;
  right: 0;
  left: 0;
  z-index: 1030;
  box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.2); }

.error {
  border: 1px solid red !important; }

.arrow-up {
  position: fixed;
  right: 50px;
  bottom: 50px;
  z-index: 99999999;
  cursor: pointer;
  width: 50px;
  height: 50px;
  background: rgba(204, 204, 204, 0.7);
  border-radius: 50%; }
  @media (max-width: 991.98px) {
    .arrow-up {
      right: 30px;
      bottom: 30px; } }
  .arrow-up .fa {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 40px;
    color: #4D4D4D;
    transition: color 0.6s ease-in-out; }
    .arrow-up .fa:hover {
      color: #000; }

.header {
  padding: 10px 0;
  background: #F0F0F0;
  font-family: 'Fira Code', monospace;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999999; }
  .header .nav {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .header .nav .logo .fa {
      font-size: 30px;
      color: #2b2b2b; }
    .header .nav-menu {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-basis: 50%; }
      @media (max-width: 991.98px) {
        .header .nav-menu {
          display: none; } }
      @media (max-width: 991.98px) {
        .header .nav-menu__list {
          margin: 15px 0; } }
      .header .nav-menu__link {
        font-size: 16px;
        color: #2b2b2b;
        font-weight: 500;
        transition: all .3s;
        padding: 10px 20px;
        border-radius: 20px; }
        .header .nav-menu__link_active {
          display: block;
          width: 100%;
          background: #4D4D4D;
          font-weight: 600;
          color: #FFF; }
    .header .nav-menu.active {
      display: block;
      text-align: center;
      position: absolute;
      top: 50px;
      left: 0;
      width: 100%;
      background: #f0f0f0;
      z-index: 9999;
      padding: 20px 30px; }
  .header-toggle {
    width: 40px;
    height: 40px;
    cursor: pointer;
    display: none;
    position: relative; }
    @media (max-width: 991.98px) {
      .header-toggle {
        display: block; } }
    .header-toggle span {
      width: 100%;
      height: 2px;
      background: #2b2b2b;
      position: absolute;
      top: 20px !important;
      left: 0;
      transform: rotate(0);
      transition: 0.4s; }
      .header-toggle span:nth-child(1) {
        top: 8px !important; }
      .header-toggle span:nth-child(2), .header-toggle span:nth-child(3) {
        top: 20px  !important; }
      .header-toggle span:nth-child(4) {
        top: 32px !important; }
    .header-toggle.active span:nth-child(1), .header-toggle.active span:nth-child(4) {
      left: 50%;
      width: 0;
      top: 6px; }
    .header-toggle.active span:nth-child(2) {
      transform: rotate(45deg); }
    .header-toggle.active span:nth-child(3) {
      transform: rotate(-45deg); }

